@charset "UTF-8";
@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,regular&display=swap);
body {
    font: 300 1.1111111111vw "Roboto";
    color: #AAAAAA;
}

h1,
h2,
h4,
h5 {
    color: #1A1A1A;
    font-weight: 300;
}

h2 {
    font-size: 3.3333333333vw;
    line-height: 1.2;
}

h3 {
    color: #000;
    font-size: 3.1944444444vw;
    line-height: 1.1;
    font-weight: 400;
}

h4 {
    font-size: 2.2222222222vw;
    line-height: 1.2;
}

h5 {
    font-family: "Roboto";
    font-size: 1.6666666667vw;
    line-height: 1;
}

h6 {
    font-size: 0.9722222222vw;
    line-height: 0.6;
    letter-spacing: 0.5px;
    color: #fff;
}

a {
    letter-spacing: 2px;
    font-size: 0.8333333333vw;
    color: #AAAAAA;
}
a:hover {
    text-decoration: underline;
}

p {
    font-size: 1.6666666667vw;
    line-height: 1.5;
}

html, body {
    height: 100%;
}

.wrapper {
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

main {
    flex: 1 1 auto;
}

[class*=container] {
    max-width: 1340px;
    margin: 0 auto;
    padding: 0 15px;
}

.header__container {
    max-width: 1440px;
}

.header__top {
    background-color: #F5F5F5;
}

.contacts {
    display: flex;
    justify-content: flex-end;
    margin-right: -25px;
}

.contacts__item {
    padding-right: 25px;
}

.contacts__link {
    text-transform: uppercase;
}

.header__navigation {
    display: flex;
}

.header__white {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25px 0 25px 55px;
}

.header__logo {
    z-index: 100;
}
.header__logo:hover {
    opacity: 0.7;
}

.navigation__list {
    display: flex;
    margin-left: -100px;
}

.navigation__item {
    padding-left: 100px;
}

.navigation__link {
    text-transform: uppercase;
    color: #000;
}

.location {
    display: flex;
}

.location__list {
    margin-left: -20px;
    display: flex;
}

.location__item {
    padding-left: 20px;
}

.location__link {
    margin-left: 20px;
    text-transform: uppercase;
}

span {
    transition: color 0.5s ease;
}
span:hover {
    opacity: 0.7;
}

.design__img {
    height: 800px;
}

.design__content {
    display: flex;
    padding: 100px 0 0 110px;
}

.design__title {
    flex: 0 0 auto;
}

.design__text {
    padding-left: 150px;
    max-width: 680px;
}

.four-rooms__list {
    display: flex;
    flex-wrap: wrap;
    padding-top: 190px;
    margin-left: -45px;
}

.four-rooms__item {
    padding: 25px 0 0 45px;
}

.four-rooms__title {
    padding-top: 15px;
}

.room-img {
    max-width: 100%;
}

.brands__list {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 215px 0 185px 0;
}

.icons__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.brands__subtitle {
    margin: 30px 0 125px 0;
    color: rgba(74, 74, 74, 0.2901960784);
}

.icons__item {
    width: 16.66%;
    margin-bottom: 50px;
}

.icons__link {
    transition: color 0.5 ease;
}
.icons__link:hover {
    opacity: 0.7;
}

.icons-img {
    max-width: 100%;
}

.products {
    display: flex;
    flex-direction: column;
    padding: 85px 55px 240px 105px;
}

.products__text {
    padding: 25px 0 75px 0;
    color: #46856E;
    font-size: 14px;
}

.product__list {
    display: flex;
    justify-content: space-between;
    margin-left: -20px;
}

.product__item {
    width: 33.33%;
    padding-left: 20px;
}

.product__img {
    text-align: center;
}

.product-img {
    max-width: 100%;
}

.product__subtitle {
    padding: 20px 0;
}

.product__column {
    display: flex;
    flex-direction: column;
}

.product__content {
    font-size: 16px;
    line-height: 24px;
}

.reviews__section {
    height: 900px;
}

.reviews__imeges {
    padding: 120px 0 0 200px;
}

.reviews__list {
    display: flex;
    flex-direction: column;
    max-width: 220px;
}

.reviews__items {
    margin-bottom: 40px;
}

.reviews__text {
    color: #fff;
    font-size: 20px;
    line-height: 32px;
    margin: 10px 0;
}

.reviews__icon {
    transition: opacity 0.5s ease 0s;
}
.reviews__icon:hover {
    opacity: 0.7;
}

.reviews__sign {
    color: #fff;
}

.stars__list {
    display: flex;
}

.stars__item {
    transition: opacity 0.5s ease 0s;
}
.stars__item:hover {
    opacity: 0.7;
}

.work__section {
    padding-top: 140px;
}

.gray__title {
    color: #AAAAAA;
    max-width: 1055px;
    padding: 100px 0 0 205px;
}

.black__title {
    margin: 30px 0 145px 210px;
}

.work__list {
    padding-left: 55px;
}

.work__items {
    display: flex;
    margin-bottom: 65px;
}

.work__card {
    padding: 258px 90px;
}

.work__title {
    color: #1A1A1A;
}

.work__text {
    margin-top: 25px;
    font-size: 16px;
    line-height: 24px;
    max-width: 300px;
}

.work-img {
    object-fit: cover;
}

.block1 {
    order: 2;
}

.risk__img {
    margin-bottom: 150px;
}

.risk__content {
    background-color: rgba(242, 242, 242, 0.9490196078);
    background-repeat: 2px;
    display: flex;
    flex-direction: column;
    text-align: center;
    margin: 230px 0 0 40px;
}

.risk__title {
    padding-top: 65px;
    text-align: center;
}

.risk__list-col {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 65px 0 65px -165px;
}
.risk__list-col.slider .risk__item {
    padding-left: 0;
}

.risk__item-col {
    padding-left: 165px;
}

.risk__subtitle {
    margin-top: 20px;
    color: #1A1A1A;
    font-size: 12px;
}

.risk__sign {
    margin: 15px 0;
}

.footer__column.mobile {
    display: flex;
    flex-wrap: wrap;
}
.footer__column.mobile .item {
    width: 33.33%;
}

.footer {
    background-color: #2B2B2B;
    margin-top: 80px;
}

.mobile-footer {
    display: none;
}

.footer__section {
    padding: 100px 110px;
    position: relative;
}

.footer__content {
    display: flex;
    justify-content: space-between;
}

.footer__logo {
    transition: opacity 0.5s ease 0s;
}
.footer__logo:hover {
    opacity: 0.7;
}

.about {
    margin-bottom: 45px;
}

.support {
    margin-bottom: 20px;
}

.footer__title {
    margin-bottom: 20px;
}

.addres {
    line-height: 15px;
    font-size: 12px;
}

.social__list {
    display: flex;
}

.social__items {
    padding-left: 15px;
}
.social__items:first-child {
    padding-left: 0;
}

.footer__column4 {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.singup {
    text-align: end;
}

.footer__signup {
    font-size: 14px;
}

@media (max-width: 768px) {
    .hamburger {
        z-index: 100;
        position: relative;
        flex: 0 0 60px;
        height: 40px;
        width: 60px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        left: 25%;
        top: 20%;
    }
    .hamburger::before, .hamburger::after {
        content: "";
        background-color: #2B2B2B;
        height: 4px;
    }
    .hamburger span {
        height: 4px;
        background-color: #2B2B2B;
    }
    .navigation {
        z-index: 50;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 90px;
        background-color: rgba(242, 242, 242, 0.9490196078);
    }
    .navigation__body {
        padding: 20px;
    }
    .navigation__list {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: absolute;
        transform: translateX(-50%);
        left: 10%;
        top: 100%;
        opacity: 0;
        background-color: rgba(242, 242, 242, 0.9490196078);
        padding: 20px 80px;
        visibility: hidden;
        transition: opacity 0.3s ease, visibility 0.3s ease;
    }
    .navigation__list.active {
        visibility: visible;
        opacity: 1;
    }
    .navigation__item {
        margin-bottom: 40px;
    }
    .navigation__item:last-child {
        margin-bottom: 0;
    }
    .navigation__link {
        font-size: 32px;
    }
    .header__logo {
        position: fixed;
        top: 5%;
    }
    .location__list {
        position: fixed;
        z-index: 100;
        right: 30%;
        top: 10%;
    }
    .design__text {
        max-width: 400px;
        padding-left: 80px;
    }
    .four-rooms__list {
        padding-top: 50px;
    }
    .brands__subtitle {
        margin-bottom: 50px;
    }
    .brands__list {
        padding-top: 50px;
    }
    .icons__item {
        width: 25%;
    }
    .reviews__title {
        padding-top: 80px;
    }
    .reviews__list {
        padding: 50px 0 50px 50px;
        max-width: 250px;
    }
    .product__item {
        width: 30%;
    }
    .work__section {
        padding: 80px 55px 0 55px;
    }
    .black__title {
        margin-bottom: 30px;
    }
    .work__items {
        flex-direction: column;
    }
    .work__card {
        padding: 50px 0;
    }
    .risk__list-col {
        margin-left: -100px;
    }
    .risk__item-col {
        padding-left: 100px;
    }
    .footer__section {
        flex-wrap: wrap;
    }
    .footer__logo {
        width: 35%;
    }
    .footer__list {
        margin-bottom: 50px;
    }
    .footer__column3 {
        margin-bottom: 50px;
    }
}
@media (max-width: 576px) {
    .hamburger {
        height: 30px;
        width: 44px;
        left: 30%;
        top: 20%;
    }
    .footer__section {
        display: none;
    }
    .mobile-footer {
        display: flex;
    }
    .mobile__list {
        display: flex;
        flex-direction: column;
        padding: 50px;
    }
    .mobile__item {
        margin-bottom: 20px;
    }
    .design__text {
        padding-left: 50px;
        max-width: 300px;
    }
    .four-rooms__item {
        width: 100%;
    }
    .icons__item {
        width: 30%;
    }
    .product__list {
        flex-wrap: wrap;
    }
    .product__item {
        width: 100%;
        margin-bottom: 50px;
    }
    .risk__list-col {
        margin-left: -95px;
    }
    .risk__item-col {
        padding-left: 95px;
    }
}
