.button {
    max-width: 145px;
    background-color: #46856E;
    font-size: 12px;
    line-height: 24px;
    letter-spacing: 2px;
    color: #fff;
    transition: color 0.5s ease 0s , background 0.5s ease 0s;
}
.button:hover {
    background-color: #2B2B2B;
}

.risk__button {
    color: #fff;
    padding: 15px 25px;
    display: block;
    margin: 0 auto;
}

.select {
    border: none;
    background-color: #F5F5F5;
}

.footer__button {
    position: absolute;
    right: -120px;
    top: 75%;
    text-align: center;
    transform: rotate(-90deg);
    text-transform: uppercase;
    font-size: 12px;
}

.input__footer {
    height: 55px;
    width: 360px;
    border: 1px solid rgba(255, 255, 255, 0.4);
    border-radius: 3px;
    background-color: #2B2B2B;
}

.select__footer {
    margin-top: 15px;
    background-color: #2B2B2B;
    border: none;
    display: block;
    margin-left: auto;
}

.custom-checkbox {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.custom-checkbox + label {
    display: inline-flex;
    align-items: center;
    user-select: none;
    max-width: 400px;
    margin: 45px 0;
    text-transform: uppercase;
    font-size: 12px;
}

.custom-checkbox + label::before {
    content: "";
    display: inline-block;
    width: 1.5em;
    height: 1.5em;
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid #AAAAAA;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
    position: relative;
    margin: -55px 20px 0 20px;
}

.custom-checkbox:checked + label::before {
    border-color: #AAAAAA;
    background-color: #2B2B2B;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
}

.slick-dots {
    display: flex;
    text-align: center;
    justify-content: center;
}

.slick-prev,
.slick-next {
    position: absolute;
    top: 50%;
    z-index: 10;
    font-size: 0;
    border: none;
    height: 40px;
    padding: 0 50px;
}

.slick-prev {
    left: 0;
    background: url("../img/arrow-left.svg") 0 0 no-repeat;
    margin-left: 50px;
}

.slick-next {
    right: 0;
    background: url("../img/arrow-right.svg") 0 0 no-repeat;
}

.slick-dots button {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    border: none;
    font-size: 0;
    margin-left: 10px;
}

.slick-active button {
    background-color: #46856E;
}

.slick-track {
    padding-bottom: 25px;
}

@media (max-width: 768px) {
    .button {
        left: 40%;
    }
    .custom-checkbox + label {
        max-width: 300px;
    }
    .custom-checkbox + label::before {
        margin: -10px 20px 0 0;
    }
}
@media (max-width: 576px) {
    .button {
        left: 37%;
    }
    .mobile-select {
        background-color: #2B2B2B;
        font-size: 16px;
        width: 300px;
    }
}
